import React, { useRef } from 'react'
import DeleteIcon from '@material-ui/icons/Delete';
import PostAddIcon from '@material-ui/icons/PostAdd';
import Avatar from '@material-ui/core/Avatar';
import { useDispatch } from 'react-redux';
import { addPost } from './../store/action/postActions';
import TouchAppIcon from '@material-ui/icons/TouchApp';
import ImageIcon from '@material-ui/icons/Image';
export default function EditPost() {
    const dispatch = useDispatch('')
    const ref = useRef('')
    const savePost = () => {
        dispatch(addPost({ data: ref.current.innerText, user_id: 22 }))

    }
    const deletePost = () => {
        ref.current.innerText = "砖转驻 转 住驻专 砖"

    }

    return (
        <div>
            <div className="paper">
                {/* <Avatar className="avatar" /> */}
                <div className="pattern">
                    <p ref={ref} contentEditable className="content">

                        转 砖 转 注 砖转 注专转  抓   专转 注  转 祝  转 转注转.   专爪 拽专转, 砖转专拽   驻砖专. 砖转 转驻  砖专拽注 爪注拽转 转 驻转 转   砖 砖 注 砖 砖专转.   转  转 转拽  专爪 转 砖
                    </p>

                </div>
                <div className="flex align-center"><button className="triger"> <TouchAppIcon /> 住驻 专专</button><div className="flex align-center"> <ImageIcon /><span>注 转</span></div> </div>
                <div className="flex align-center">
                    {/* <DeleteIcon onClick={deletePost} /> */}
                    {/* <PostAddIcon /> */}
                    <button onClick={savePost}>砖转驻</button>


                </div>
            </div>


        </div>
    )
}
